/**
 * Enables material icons on <i> tags
 * Make sure to include a stylesheet link to material icons:
 * <link rel="stylesheet" href="https://fonts.googleapis.com/icon?family=Material+Icons" type="text/css" />
 */
i {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 1em;
  display: inline-block;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: 'liga';
  vertical-align: middle;
  transform: translateY(-0.1em);
}
body {
  font-family: sans-serif;
  background-color: #f6f6f6;
}
article {
  max-width: 960px;
  margin: 1.5em auto;
}
section {
  margin: 1.5em 0;
  padding: 1.5em;
  background-color: #fff;
}
h1,
h2 {
  color: #333;
  font-weight: 300;
}
.example-text-input {
  display: inline-block;
  position: relative;
  padding: 1em 0;
  max-width: 100%;
  margin: 1em;
}
.example-text-input input,
.example-text-input textarea,
.example-text-input select {
  display: block;
  padding: 0.25em 0;
  line-height: 1.5;
  width: 100%;
  border: 0 solid #ccc;
  border-bottom-width: 1px;
  transition: all 0.25s;
  overflow: hidden;
  box-sizing: border-box;
  max-width: 100%;
  background: transparent;
}
.example-text-input input:focus,
.example-text-input textarea:focus,
.example-text-input select:focus {
  outline: none;
  box-shadow: none;
  border-color: #336699;
}
.example-text-input input:required,
.example-text-input textarea:required,
.example-text-input select:required {
  outline: none;
  box-shadow: none;
}
.example-text-input input:invalid,
.example-text-input textarea:invalid,
.example-text-input select:invalid {
  outline: none;
  box-shadow: none;
}
.example-text-input input::placeholder,
.example-text-input textarea::placeholder,
.example-text-input select::placeholder {
  display: none;
}
.example-text-input select {
  height: calc(2em + 1px);
}
.example-text-input label {
  position: absolute;
  left: 0;
  line-height: 1;
  transition: all 0.25s;
  opacity: 0.5;
  top: 1.5em;
}
.example-text-input .bar {
  position: relative;
  display: block;
  width: 100%;
}
.example-text-input .bar:before,
.example-text-input .bar:after {
  content: '';
  height: 2px;
  width: 0;
  bottom: 0;
  position: absolute;
  background: #336699;
  transition: all 0.25s;
}
.example-text-input .bar:before {
  left: 50%;
}
.example-text-input .bar:after {
  right: 50%;
}
.example-text-input input:focus ~ label,
.example-text-input textarea:focus ~ label,
.example-text-input select:focus ~ label,
.example-text-input.not-empty > label {
  font-size: 0.75em;
  top: 0.25em;
}
.example-text-input input:focus ~ label,
.example-text-input textarea:focus ~ label,
.example-text-input select:focus ~ label {
  opacity: 1;
  color: #336699;
}
.example-text-input input:focus ~ .bar:before,
.example-text-input textarea:focus ~ .bar:before,
.example-text-input select:focus ~ .bar:before,
.example-text-input input:focus ~ .bar:after,
.example-text-input textarea:focus ~ .bar:after,
.example-text-input select:focus ~ .bar:after {
  width: 50%;
}
.example-text-input input:invalid:focus,
.example-text-input textarea:invalid:focus,
.example-text-input select:invalid:focus {
  border-color: #660000;
}
.example-text-input input:invalid:focus ~ label,
.example-text-input textarea:invalid:focus ~ label,
.example-text-input select:invalid:focus ~ label {
  color: #660000;
}
.example-text-input input:invalid:focus ~ .bar:before,
.example-text-input textarea:invalid:focus ~ .bar:before,
.example-text-input select:invalid:focus ~ .bar:before,
.example-text-input input:invalid:focus ~ .bar:after,
.example-text-input textarea:invalid:focus ~ .bar:after,
.example-text-input select:invalid:focus ~ .bar:after {
  background: #660000;
}
.example-text-input .message {
  position: absolute;
  top: 100%;
  transform: translateY(-1em);
  left: 0;
  font-size: 0.75em;
  line-height: 1;
}
.example-text-input .validation-message {
  color: #660000;
}
