.example-button {
  border-radius: 0.2em;
  line-height: 1.5;
  border: 0;
  padding: 0.25em 1em;
  background-color: #666;
  color: #fff;
  cursor: pointer;
  position: relative;
}
.example-button.danger {
  color: #660000;
}
.example-button.primary {
  background-color: #485;
}
.example-button.primary.danger {
  background-color: #660000;
  color: #fff;
}
.example-button.secondary {
  background-color: transparent;
  color: #485;
}
.example-button.secondary.danger {
  color: #660000;
}
.example-button:hover:after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  opacity: 1;
}
.example-button.secondary:hover {
  text-decoration: underline;
}
.example-button[disabled] {
  opacity: 0.5;
  cursor: not-allowed;
}
.example-button[disabled]:hover:after {
  opacity: 0;
}
.example-button-group > * {
  border-radius: 0;
  border-left: 1px solid rgba(255, 255, 255, 0.5);
}
.example-button-group > *:first-child {
  border-left: 0;
  border-radius: 0.2em 0 0 0.2em;
}
.example-button-group > *:last-child {
  border-radius: 0 0.2em 0.2em 0;
}
.example-checkbox {
  display: inline-flex;
  flex-flow: row nowrap;
  padding: 0.25em 0;
  align-items: center;
  margin: 1em;
}
.example-checkbox input[type="checkbox"] {
  margin: 0.25em 0.5em 0.25em 0.25em;
}
.example-checkbox input[type="checkbox"]:disabled ~ label {
  opacity: 0.5;
}
.example-checkbox-group {
  display: inline-block;
  flex-flow: row wrap;
  margin: 1em;
}
.example-checkbox-group > label {
  display: block;
  font-size: 0.75em;
}
.example-checkbox-group > .checkboxes {
  display: block;
}
.example-checkbox-group > .checkboxes > * {
  margin-right: 0.5em;
}
.example-radio-button {
  display: inline-flex;
  flex-flow: row nowrap;
  padding: 0.25em 0;
  align-items: center;
  margin: 1em;
}
.example-radio-button input[type="radio"] {
  margin: 0.25em 0.5em 0.25em 0.25em;
}
.example-radio-button input[type="radio"]:disabled ~ label {
  opacity: 0.5;
}
.example-radio-group {
  display: inline-block;
  flex-flow: row wrap;
  margin: 1em;
}
.example-radio-group > label {
  display: block;
  font-size: 0.75em;
}
.example-radio-group > .radio-buttons {
  display: block;
}
.example-radio-group > .radio-buttons > * {
  margin-right: 0.5em;
}
.example-dropdown {
  position: relative;
}
.example-dropdown .popup {
  position: absolute;
  top: 100%;
  visibility: hidden;
}
.example-dropdown.open .popup {
  visibility: visible;
}
.example-bouncer {
  display: inline-block;
  vertical-align: middle;
  transform: translateY(-0.1em);
}
.example-bouncer .container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}
.example-bouncer .container div {
  width: 1em;
  height: 1em;
  background-color: #333;
  margin: 0.125em;
  border-radius: 100%;
  display: block;
  -webkit-animation: og-spa-bouncer-bounce-delay 1.4s infinite ease-in-out both;
  animation: og-spa-bouncer-bounce-delay 1.4s infinite ease-in-out both;
}
.example-bouncer .container .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.example-bouncer .container .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
@-webkit-keyframes og-spa-bouncer-bounce-delay {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
  }
}
@keyframes og-spa-bouncer-bounce-delay {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
.example-spinner {
  display: inline-block;
  width: 1em;
  height: 1em;
  vertical-align: middle;
  transform: scale(2) translateY(-0.1em);
  transform-origin: center center;
}
.example-spinner svg {
  width: 100%;
  height: 100%;
  animation: rotate 2s linear infinite;
}
.example-spinner svg circle {
  stroke-dasharray: 1,200;
  stroke-dashoffset: 0;
  animation: dash 1.5s ease-in-out infinite;
  stroke-linecap: round;
}
@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35;
  }
  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124;
  }
}
.example-data-table {
  display: block;
}
.example-data-table table {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
}
.example-data-table th,
.example-data-table td {
  padding: 0.25em 0.5em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.example-data-table .align-left {
  text-align: left;
}
.example-data-table .align-center {
  text-align: center;
}
.example-data-table .align-right {
  text-align: right;
}
.example-data-table .clickable {
  cursor: pointer;
}
.example-data-table table > tbody > tr:nth-child(odd) {
  background-color: #f6f6f6;
}
.example-data-table table tbody.row-group:nth-child(odd) {
  background-color: #f6f6f6;
}
.example-data-table tbody {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.example-data-table td {
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
}
.example-data-table table > tbody > tr:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
.example-data-table table tbody.row-group:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
